import Vue from 'vue'
import VueRouter from 'vue-router'
import { routes } from './routers'
import store from '@/store'
import { getISLOGIN, setTitle, setToken, clearToken, getToken } from '@/libs/util'
import config from '@/config'
const { homeName } = config

Vue.use(VueRouter)
const router = new VueRouter({
    base: window.__POWERED_BY_QIANKUN__ ? `/${process.env.VUE_APP_CODE}` : `/web/${process.env.VUE_APP_CODE}/`,
    mode: 'history',
    scrollBehavior: () => ({ y: 0 }),
    routes
})

router.beforeEach((to, from, next) => {
    to.meta.title && setTitle(to.meta.title)

    const token =  getToken()
    const login_route = {
        name: 'login',
        params: {
            "c_url": window.location.href
        }
    }

    // 正式环境跳转统一单点登录地址， 测试环境当前项目登录
    const cusLogin = () => {
        if (config.ssoLogin) {
            next(false)
            window.location.href = config.ssoLogin
        } else {
            next(login_route)
        }
    }
    if (token) {
        if (!store.state.router.hasGetRules) {
            store.dispatch('authorization').then(rules => {
                store.dispatch('concatRoutes', rules).then(routers => {
                    router.addRoutes(routers)
                    next({...to, replace: true })
                }).catch((err) => {
                    setToken('')
                    clearToken()
                    cusLogin()
                })
            }).catch((err1) => {
                setToken('')
                clearToken()
                cusLogin()
            })
        } else {
            next()
        }
    } else {
        if (to.name === 'login' && !config.ssoLogin) next()
        else cusLogin()
    }
})


router.afterEach(to => {
    window.scrollTo(0, 0)
})

export default router
